import './index.css'
import FormSearchOrder from './FornSearchOrder'
import OrderInfo from './OrderInfo'
import { useState } from 'react'
function TimKiemDonHang() {

    const fakeInfo = {
        id: 2255,
        name: "L Anh Tun",
        phone: "0905803676",
        email: "tuanak691@gmail.com",
        orderDate: "21/04/2022",
        orderAdress: "Qun Sn Tr ,  Nng",
        paymentStatus: "Ch thanh ton",
        deliveryStatus: "Cha vn chuyn",
        totalPrice: "12.270.000",
        totalAmount: "5"

    }
    const [showInput, setShowInput] = useState('phone')

    const handleClick = (e) => {
        setShowInput(e.target.value)
    }

    return (
        <div className="tim-kiem-wrapper py-5">
            <div className="container">
                <div className='row'>

                    <div className='col-md-5'>
                        <div className='container'>
                            <FormSearchOrder
                                showInput={showInput}
                                handleClick={handleClick}
                            />
                        </div>
                    </div>

                    <div className='col-md-7 '>
                        <div className='container'>
                            <OrderInfo
                                {...fakeInfo}
                            ></OrderInfo>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default TimKiemDonHang